.sidebar__twitterIcon {
  color: var(--twitter-color);
  font-size: 30px !important;
  margin-left: 20px;
  margin-bottom: 20px;
}

.sidebar__tweet {
  background-color: var(--twitter-color) !important;
  border: none !important;
  color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  border-radius: 30px !important;
  height: 50px !important;
  margin-top: 20px !important;
}

.sidebarOption {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.sidebarOption:hover {
  background-color: #e8f5fe;
  border-radius: 30px;
  color: var(--twitter-color);
  transition: color 100ms ease-out;
}

.sidebarOption > .MuiSvgIcon-root {
  padding: 20px;
}

.sidebarOption > h2 {
  font-weight: 800;
  font-size: 20px;
  margin-right: 20px;
}

.sidebarOption--active {
  color: var(--twitter-color);
}

.signinPage{
    min-width: 100vw;
    min-height: 100vh;
    overflow: hidden;
}


.signin__top{
    width: 100%;
    height: 88vh;
    position: relative;
    display: flex;
    overflow: hidden;
}

.signin__left{
    position: absolute;
    top: 0;
    left: 0;
    width: 53%;
    height: 100%;
}
.img > img{
    position: absolute;
    object-fit: contain;
    width: 100%;
    height: 100%;
}

.signin__right{
    position: absolute;
    top: 0;
    right: 0;
    width: 43%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}


.signin__right > .MuiSvgIcon-root{
    font-size: 60px;
    color:var(--twitter-color);
}

.signin__right h2{
    font-family: 'Montserrat', sans-serif;
    font-size: 70px;
}

.signin__right h3{
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
}

.signin__right span{
    font-size: 20px;
}

.signin__right button{
    width: 400px;
    border-radius: 30px;
    height: 50px;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    outline: none;
}

.signin__button{
    border: none;
    color: white;
    background-color: #1A91DA;
}

.login__button{
    border: 1px solid var(--twitter-color);
    color: var(--twitter-color);
    background-color: white;
}

.signin__bottom{
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 20px 20px;
}

.signin__bottom > .links{
    width: 100%;
    display: flex;
    
    align-items: center;
    justify-content: space-between;
}

.signin__bottom > .links > a,span{
    text-decoration: none;
    color: gray;
}
